.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    width: 85%;
    margin: 3.44rem auto 0rem;
}

.gpt3__navbar-links {
    display: flex;
    align-items: center;
    gap: 4.3rem;
}

.gpt3__navbar-links_container {
    display: flex;
    gap: 3rem;

}

.gpt3__navbar-links_logo {
    width: 3.90981rem;
    height: 1.001rem;
}

.gpt3__navbar-links_logo img {
    width: 100%;
}

.gpt3__navbar-links_container>p,
.gpt3__navbar-sign>p,
.gpt3__navbar-sign button,
.gpt3__navbar-menu_container-links p,
.gpt3__navbar-menu_container-links-sign p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.125rem;
    cursor: pointer;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 1.36rem;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container-links-sign button {
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.125rem;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 1rem 2.75rem;
}

.gpt3__navbar-menu-sign-container {
    display: flex;
    align-items: center;
    gap: 1.5rem;
}
.gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1090px) {
    .gpt3__navbar-links_container {
        display: none;
    }

    .gpt3__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__navbar {
        padding: 2rem;
    }

    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 20px;
    }

    .gpt3__navbar-menu_container-links-sign {
        display: block;
    }
}